import * as Consts from './Consts';
import * as InputValuesHelpers from './InputValuesHelper';

export async function GetUserId (Auth) {
  // get user token
  const idToken = (await Auth.currentSession()).getIdToken();
  // get user id
  return idToken.decodePayload().sub;
}

export const GetPostData = (inputValues, fieldDefinitions, dataSection) => {
  const postData = {};
  const fields = fieldDefinitions.filter(item => item.DataSection === dataSection);

  for (const field of fields) {
    var validationRules = [];
    if (typeof field.DependsOn === 'string') {
      validationRules.push({ field: field.DependsOn, value: field.DependsOnValue });
    } else if (typeof field.DependsOn === 'object' && Array.isArray(field.DependsOn)) {
      validationRules = field.DependsOn;
    }

    var notMatching = false;
    for (const validationRule of validationRules) {
      const dependsOnFieldName = validationRule.field;
      if (dependsOnFieldName !== undefined) {
        const dependsOn = fieldDefinitions.find(item => item.FieldName === dependsOnFieldName);
        if (dependsOn !== undefined) {
          if (dependsOn.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
            const dependsOnValue = (validationRule.value !== undefined ? validationRule.value : Consts.OPTION_YES_VALUE);

            if (InputValuesHelpers.IsDependsOnMet(inputValues[dependsOnFieldName], dependsOnValue) !== true) {
              if (field.DataType === Consts.DATA_TYPE_STRING) {
                postData[field.DataField] = Consts.DATA_TYPE_STRING_DEFAULT_VALUE;
              } else if (field.DataType === Consts.DATA_TYPE_BOOLEAN) {
                postData[field.DataField] = Consts.DATA_NULL;
              } else if (field.DataType === Consts.DATA_TYPE_FILES) {
                postData[field.DataField] = [];
              } else {
                postData[field.DataField] = Consts.DATA_NULL;
              }

              notMatching = true;
              break;
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_MULTIPLE_CHIOSES) {
            let dependsOnInvalid = false;
            if (inputValues[dependsOnFieldName] === undefined || validationRule.value === undefined) {
              dependsOnInvalid = true;
            } else {
              if (validationRule.value !== Consts.OPTION_ANY_VALUE &&
                InputValuesHelpers.IsDependsOnMet(inputValues[dependsOnFieldName], validationRule.value) !== true) {
                dependsOnInvalid = true;
              }
            }

            if (dependsOnInvalid === true) {
              if (field.DataType === Consts.DATA_TYPE_STRING) {
                postData[field.DataField] = Consts.DATA_TYPE_STRING_DEFAULT_VALUE;
              } else if (field.DataType === Consts.DATA_TYPE_BOOLEAN) {
                postData[field.DataField] = Consts.DATA_NULL;
              } else if (field.DataType === Consts.DATA_TYPE_FILES) {
                postData[field.DataField] = [];
              } else {
                postData[field.DataField] = Consts.DATA_NULL;
              }

              notMatching = true;
              break;
            }
          } else if (dependsOn.Type === Consts.FIELD_TYPE_CUSTOME_SELECT || dependsOn.Type === Consts.FIELD_TYPE_OPTIONS) {
            let dependsOnInvalid = false;
            if (inputValues[dependsOnFieldName] === undefined || validationRule.value === undefined) {
              dependsOnInvalid = true;
            } else if (validationRule.value !== Consts.OPTION_ANY_VALUE &&
              InputValuesHelpers.IsDependsOnMet(inputValues[dependsOnFieldName], validationRule.value) !== true) {
              dependsOnInvalid = true;
            }

            if (dependsOnInvalid === true) {
              if (field.DataType === Consts.DATA_TYPE_STRING) {
                postData[field.DataField] = Consts.DATA_TYPE_STRING_DEFAULT_VALUE;
              } else if (field.DataType === Consts.DATA_TYPE_BOOLEAN) {
                postData[field.DataField] = Consts.DATA_NULL;
              } else if (field.DataType === Consts.DATA_TYPE_FILES) {
                postData[field.DataField] = [];
              } else {
                postData[field.DataField] = Consts.DATA_NULL;
              }

              notMatching = true;
              break;
            }
          }
        }
      }
    }

    if (notMatching) {
      continue;
    }

    if (field.Type === Consts.FIELD_TYPE_TABLE) {
      var totalRows = 0;
      if (field.Rows !== undefined) {
        if (typeof field.Rows === 'number') {
          totalRows = field.Rows;
        } else if (typeof field.Rows === 'string') {
          totalRows = parseInt(inputValues[field.Rows]);
        }
      }

      const rowNumberCol = field.Cols.find(col => col.Type === Consts.FIELD_TYPE_TABLE_ROW_NUMBER);
      const rowNumberField = rowNumberCol === undefined ? 'Unknown' : rowNumberCol.DataField;

      var dataItems = [];
      for (var row = 1; row <= totalRows; row++) {
        var tableFieldDefs = [];
        for (const element of field.Cols) {
          if (element.FieldName !== undefined) {
            var newField = {
              ...element,
              FieldName: `${element.FieldName}_${row}`,
              DataSection: field.DataSection
            };

            if (element.DependsOn !== undefined) {
              var tableFieldDependsOn = [];
              if (Array.isArray(element.DependsOn)) {
                for (const dependsOnItem of element.DependsOn) {
                  if (dependsOnItem.row_field !== undefined && dependsOnItem.row_value !== undefined) {
                    tableFieldDependsOn.push({ field: `${dependsOnItem.row_field}_${row}`, value: dependsOnItem.row_value });
                  } else if (dependsOnItem.field !== undefined && dependsOnItem.value !== undefined) {
                    tableFieldDependsOn.push(dependsOnItem);
                  }
                }

                newField = { ...newField, DependsOn: tableFieldDependsOn };
              }
            }
            tableFieldDefs.push(newField);
          }
        }

        const tableRowPostData = GetPostData(inputValues, tableFieldDefs, dataSection);
        dataItems.push({ ...tableRowPostData, [rowNumberField]: row });
      }

      postData[field.DataField] = dataItems;
      continue;
    }

    if (inputValues[field.FieldName] !== undefined && inputValues[field.FieldName] !== null) {
      if (field.DataType === Consts.DATA_TYPE_STRING) {
        postData[field.DataField] = inputValues[field.FieldName];
      } else if (field.DataType === Consts.DATA_TYPE_BOOLEAN && field.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
        postData[field.DataField] = (inputValues[field.FieldName] === Consts.OPTION_YES_VALUE);
      } else if (field.DataType === Consts.DATA_TYPE_BOOLEAN && field.Type === Consts.FIELD_TYPE_SINGLE_OPTION) {
        postData[field.DataField] = (inputValues[field.FieldName] === true);
      } else if (field.DataType === Consts.DATA_TYPE_FILES) {
        postData[field.DataField] = inputValues[field.FieldName];
      } else if (field.DataType === Consts.DATA_TYPE_NUMBER) {
        postData[field.DataField] = parseFloat(inputValues[field.FieldName]);
      } else {
        postData[field.DataField] = inputValues[field.FieldName];
      }
    } else {
      if (field.DataType === Consts.DATA_TYPE_STRING) {
        postData[field.DataField] = Consts.DATA_TYPE_STRING_DEFAULT_VALUE;
      } else if (field.DataType === Consts.DATA_TYPE_BOOLEAN) {
        postData[field.DataField] = Consts.DATA_NULL;
      } else if (field.DataType === Consts.DATA_TYPE_FILES) {
        postData[field.DataField] = [];
      } else {
        postData[field.DataField] = Consts.DATA_NULL;
      }
    }
  }

  return postData;
};
