import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class OtherFeatures extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Other' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFO_611_Additional_Features' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFO_223_Exhaust_Fan_Location' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFO_223_Other_Explanation' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFO_776_Assigned_Storage_Details' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFO_196_Has_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFO_196_Defects_Explanation' })}
        </div>
      </>
    );
  }
}
