import React from 'react';
import * as Consts from '../../../shared/Consts';
import imageMarker from '../../../images/marker.svg';
import FooterMiddleStep from './FooterMiddleStep';
import * as InputHelper from '../../Shared/InputHelper';
import * as FieldDef from '../../../shared/FieldDefinition/Listing';
import ClosingCostsCalc from '../../../shared/ClosingCostsCalc';
import numeral from 'numeral';

export default class ListingVerification extends React.Component {
  render () {
    const {
      CurrentTab,
      Property,
      InputValues
    } = this.props;
    if (CurrentTab !== Consts.ListingVerification) {
      return null;
    }

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.ListingFields
    };

    const linkStyle = { fontSize: '15px', color: 'blue' };

    const primaryPhone = InputValues.Listing_LMH_698_354_Primary_Phone;
    const primaryEmail = InputValues.Listing_LMH_698_362_Email_Address;
    const listPrice = InputValues.Listing_LMF_432_List_Price;
    var mailingAddress = 'Same as the property address';
    if (InputValues.Listing_LMH_385_MailingAddressSameAsPropertyAddress === Consts.OPTION_NO_VALUE) {
      mailingAddress = `${InputValues.Listing_LMH_257_612_Street_Address}, `;
      mailingAddress += `${InputValues.Listing_LMH_257_992_City}, `;
      mailingAddress += `${InputValues.Listing_LMH_257_992_State} ${InputValues.Listing_LMH_257_677_Postal_Code}`;
    }

    var streetAddress;
    var city;
    var state;
    var zip;
    // var vestingType;
    var ownerNames = [];
    if (Property !== undefined && Property[Consts.DATA_SECTION_PROPERTY_DATA] !== undefined) {
      const PropertyData = Property[Consts.DATA_SECTION_PROPERTY_DATA];

      if (PropertyData !== undefined) {
        zip = '';
        if (PropertyData.Zip9 !== null && PropertyData.Zip9 !== undefined) {
          const zipSections = PropertyData.Zip9.split('-');
          if (zipSections.length > 0) {
            zip = zipSections[0];
          }
        }
        streetAddress = PropertyData.StreetAddress;
        city = PropertyData.City;
        state = PropertyData.State;

        // vestingType = PropertyData.VestingOwnershipRight;
        ownerNames = PropertyData.OwnerNames.split('/');
      }
    }

    const closingCosts = ClosingCostsCalc(
      this.listPrice,
      InputValues.Listing_LMF_088_Total_Loans,
      city);

    var ListFee = '-';
    // var IncentiveCredit = '-';
    // var BuyerAgentFee = '-';

    // var CityTransferTaxRate = '-';
    // var CityTransferTax = '-';
    // var CountyTransferTaxRate = '-';
    // var CountyTransferTax = '-';

    // var EscrowFee = '-';
    // var TitleFee = '-';

    // var NaturalHazardReport = '-';
    // var RegulationCompliance = '-';

    // var HomeWarranty = '-';

    // var SurpassNet = '-';
    // var SurpassSave = '-';
    // var AdvantageNet = '-';
    // var AdvantageSave = '-';

    if (closingCosts !== undefined) {
      ListFee = closingCosts.BrokerFees.ListFee;
      // IncentiveCredit = closingCosts.BrokerFees.IncentiveCredit;
      // BuyerAgentFee = closingCosts.BrokerFees.BuyerAgentFee;

      // CityTransferTaxRate = closingCosts.Taxes.CityTransferTaxRate;
      // CityTransferTax = closingCosts.Taxes.CityTransferTax;
      // CountyTransferTaxRate = closingCosts.Taxes.CountyTransferTaxRate;
      // CountyTransferTax = closingCosts.Taxes.CountyTransferTax;

      // EscrowFee = closingCosts.ClosingCosts.EscrowFee;
      // TitleFee = closingCosts.ClosingCosts.TitleFee;

      // NaturalHazardReport = closingCosts.RegulatoryRequirements.NaturalHazardReport;
      // RegulationCompliance = closingCosts.RegulatoryRequirements.RegulationCompliance;

      // HomeWarranty = closingCosts.Other.HomeWarranty;

      // SurpassNet = closingCosts.SurpassNet;
      // SurpassSave = closingCosts.SurpassSave;
      // AdvantageNet = closingCosts.AdvantageNet;
      // AdvantageSave = closingCosts.AdvantageSave;
    }
    return (
      <>
        <div id='step4' className='main_form_info steppers'>
          <div className='title2'><div className='number_title'>04</div> Confirmation</div>
          {/* <div className='b_texts' style={{maxWidth: 'none'}}>Please review your information below. Once you are done reviewing, please submit your information</div> */}
          <div className='listing_confirm_container'>
            <div className='listing_confirm_left_panel'>
              <div className='b_texts'>Please confirm that the data below is correct for your property listing.</div>
              <div className='address animated_fastest' style={{ marginBottom: '20px', position: 'sticky -webkit-sticky', top: '0' }}>
                {
                  ownerNames.map((ownerName, index) => {
                    return (
                      <div key={index}>{ownerName.trim()}</div>
                    );
                  }
                  )
                }
                <div className='add_2'>{streetAddress}</div>
                <div className='add_2'>
                  <div className='marker'>
                    <img src={imageMarker} width='100%' alt='Marker' />
                  </div>
                  <span>{city}, {state} {zip}</span>
                </div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>List Price: </div>
                <div className='t_texts listing_confirm_item'>{numeral(listPrice).format('$0,0')}</div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>Nile List Fee: </div>
                <div className='t_texts listing_confirm_item'>{numeral(ListFee).format('$0,0')}</div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>Estimated Closing Costs *: </div>
                <div className='t_texts listing_confirm_item'>TBD</div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>Listing Period: </div>
                <div className='t_texts listing_confirm_item'>TBD</div>
              </div>
              <div>* Closing cost subject to change based on buyer and any HOA costs.</div>
            </div>
            <div className='listing_confirm_right_panel'>
              <div className='b_texts'>Please confirm that the data below is correct for your personal information.</div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>First Name: </div>
                <div className='t_texts listing_confirm_item'>TBD</div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>Last Name: </div>
                <div className='t_texts listing_confirm_item'>TBD</div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>Primary Phone: </div>
                <div className='t_texts listing_confirm_item'>{primaryPhone}</div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>Email Address: </div>
                <div className='t_texts listing_confirm_item'>{primaryEmail}</div>
              </div>
              <div className='listing_confirm_row'>
                <div className='t_texts listing_confirm_title'>Mailing Address: </div>
                <div className='t_texts listing_confirm_item'>{mailingAddress}</div>
              </div>
            </div>
          </div>

          <div className='reveals animated_fastest'>
            <div className='pro_tab alternatess'>
              <div className='tab1'>Authorization</div>
            </div>
            <div className='form_inputs' style={{ marginBottom: '50px' }}>
              <div className='required required-options'>
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'Agreed_Nile_Listing_Agreement',
                  Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Agreed_Nile_Listing_Agreement' }),
                  AfterLabelLink: <a href='https://www.nilehome.com/nile-listing-agreement/' target='_blank' style={linkStyle} rel='noopener noreferrer'>NILE Listing Agreement</a>
                })}
              </div>
              <div className='required required-options'>
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'Agreed_Disclosure_Regarding_Real_Estate_Agency_Relationship',
                  Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Agreed_Disclosure_Regarding_Real_Estate_Agency_Relationship' }),
                  AfterLabelLink: <a href='https://www.nilehome.com/disclosure-regarding-real-estate-agency-relationship/' target='_blank' style={linkStyle} rel='noopener noreferrer'>Disclosure Regarding Real Estate Agency Relationship</a>
                })}
              </div>
              <div className='required required-options'>
                {InputHelper.GenerateInput({
                  ...FieldProps,
                  FieldName: 'Agreed_NILE_Seller_Advisory',
                  Label: InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Agreed_NILE_Seller_Advisory' }),
                  AfterLabelLink: <a href='https://www.nilehome.com/seller-broker-service-agreement/' target='_blank' style={linkStyle} rel='noopener noreferrer'>NILE Seller Advisory</a>
                })}
              </div>
            </div>
          </div>

          <FooterMiddleStep
            HideSaveButton={this.props.ReadOnly}
            NextButtonText={this.props.ReadOnly === true ? 'Next' : 'Submit Offer'}
            SaveHandler={() => { this.props.SaveHandler(DataSections); }}
            PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.ListingPrice); }}
            SaveNextHandler={async () => {
              if (this.props.ReadOnly === true) {
                this.props.GoToStepHandler(Consts.ListingSubmission);
              } else {
                if (await this.props.SaveHandler(DataSections) === true) {
                  if (await this.props.UpdatePropertyStatusHandler('ListingSubmitted') === true) {
                    this.props.GoToStepHandler(Consts.ListingSubmission);
                  }
                }
              }
            }}
          />
          {this.props.ReadOnly !== true &&
            <div className='footer_alert'>
              <div className='item'>
                <div className='c_text'>put some text here if needed
                </div>
              </div>
            </div>}
        </div>
      </>

    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_PROPERTY_LMA
];
