import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/YearBuilt';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class WaterConservation extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.YearBuiltFields
    };

    if (InputValues.BYW_200_Active === undefined || InputValues.BYW_200_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='Water Conservation (PRE 1994)' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYW_270_Water_Conservation' })}
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYW_270_Water_Conservation_Explanation' })}
        </div>
      </>
    );
  }
}
