import React from 'react';
import FooterFirstStep from './FooterFirstStep';
import * as Consts from '../../../shared/Consts';
import { ContactUsForm } from '../../ContactUs/ContactUsForm';

export default class Welcome extends React.Component {
  render () {
    const { CurrentTab } = this.props;
    if (CurrentTab !== Consts.ListingWelcome) {
      return null;
    }

    return (
      <div className='steppers'>
        <div className='step_title'>Welcome to Nile.</div>
        <div className='step_texts'>We Created Nile to bring you an easeful and simple buying experience. We need to gather some information so once you find the perfect property, everything is in place to write an offer.
          <div>&nbsp;</div>
          <div>You can save your progress at any time and, if you have any questions, do not hesitate to <ContactUsForm handleSubmit={this.props.SubmitContactUsHandler}><span className='blue_text'>reach out </span></ContactUsForm>to us at <a href='mailto:hello@nilehome.com' className='blue_text'>hello@nilehome.com</a> or to your represented NILE Advisor.</div>
        </div>

        <FooterFirstStep
          SaveHandler={() => { this.props.SaveHandler(); }}
          SaveNextHandler={() => { this.props.GoToStepHandler(Consts.ListingVesting); }}
        />
      </div>
    );
  }
}
