import React from 'react';
import FooterSaveSubmit from './FooterSaveSubmit';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingConfirmPropertyDataActions extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    if (this.props.VestingSuspended) {
      return null;
    }

    return (
      <>
        {
          InputValuesHelper.GetInputValue(InputValues, 'Vesting_Property_Data_Correct') === Consts.OPTION_NO_VALUE &&
            <FooterSaveSubmit
              SaveHandler={async () => { await this.props.SaveHandler([Consts.DATA_SECTION_PROPERTY_VESTING]); }}
              PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.ListingWelcome); }}
              SubmitHandler={async () => {
                const result = await this.props.SaveHandler([Consts.DATA_SECTION_PROPERTY_VESTING]);
                if (result === true) {
                  this.props.UpdatePropertyStatusHandler('VestingSuspended');
                }
              }}
            />
        }
      </>
    );
  }
}
