import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/Listing';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingSellerProfile extends React.Component {
  render () {
    const {
      InputValues,
      NeedCompleteBy,
      NeedVerification
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.ListingFields
    };

    const propertyDataCorrect = InputValuesHelper.GetInputValue(InputValues, 'Vesting_Property_Data_Correct');
    if (propertyDataCorrect !== Consts.OPTION_YES_VALUE) {
      return null;
    }

    if (NeedCompleteBy === true && (InputValues.Vesting_Form_Completed_By === undefined || InputValues.Vesting_Form_Completed_By === '_not_listed_')) {
      return null;
    }

    const verifiedLLC = InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_LLC');
    const verifiedCorporation = InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_Corporation');
    const verifiedTrustee = InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_Trustee');

    if (NeedVerification === true &&
      verifiedLLC !== Consts.OPTION_YES_VALUE &&
      verifiedCorporation !== Consts.OPTION_YES_VALUE &&
      verifiedTrustee !== Consts.OPTION_YES_VALUE) {
      return null;
    }

    return (
      <>
        <div className='pro_tab'>
          <div className='tab1'>Tell us who you are</div>
        </div>

        {
          InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_Seller_1_active') === Consts.OPTION_YES_VALUE &&
            <div className='form_inputs'>
              <div className='t_texts'>Primary Seller</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_698_354_Primary_Phone', TitleUnderInput: true })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_698_362_Email_Address', TitleUnderInput: true })}</div>
            </div>
        }

        {
          InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_Seller_2_active') === Consts.OPTION_YES_VALUE &&
            <div className='form_inputs'>
              <div className='t_texts'>Listed Seller 2</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_Seller_2_Phone', TitleUnderInput: true })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_Seller_2_Email_Address', TitleUnderInput: true })}</div>
            </div>
        }

        {
          InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_Seller_3_active') === Consts.OPTION_YES_VALUE &&
            <div className='form_inputs'>
              <div className='t_texts'>Listed Seller 3</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_Seller_3_Phone', TitleUnderInput: true })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_Seller_3_Email_Address', TitleUnderInput: true })}</div>
            </div>
        }

        {
          InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_Seller_4_active') === Consts.OPTION_YES_VALUE &&
            <div className='form_inputs'>
              <div className='t_texts'>Listed Seller 4</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_Seller_4_Phone', TitleUnderInput: true })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_Seller_4_Email_Address', TitleUnderInput: true })}</div>
            </div>
        }

        <div className='form_inputs'>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_875_Preferredmethod' })}</div>
        </div>
        <div className='form_inputs'>
          <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_385_MailingAddressSameAsPropertyAddress' })}</div>
          {InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_385_MailingAddressSameAsPropertyAddress') === Consts.OPTION_NO_VALUE &&
            <div className='form_inputs'>
              <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_612_Street_Address' })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_992_City' })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_992_State' })}</div>
              <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_257_677_Postal_Code' })}</div>
            </div>}
        </div>

        <div className='t_texts' style={{ marginBottom: '5px' }}>
          <span>Would you like to add an Information Recipient?</span>
        </div>
        <div className='greyer' style={{ marginLeft: '0', marginBottom: '10px', lineHeight: '26px', maxWidth: '600px' }}>
          <span>Is there someone else you would like to include on communications (for example, a spouse or significant other, attorney, CPA or business advisor) as an Information Recipient? This person will only receive information, they will not be listed as a purchaser.</span>
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Inforecipient' })}
        </div>
        {InputValuesHelper.GetInputValue(InputValues, 'Listing_LMH_839_Inforecipient') === Consts.OPTION_YES_VALUE &&
          <div className='form_inputs'>
            <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_First_Name' })}</div>
            <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Last_Name' })}</div><br />
            <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Email' })}</div>
            <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMH_839_Phone' })}</div>
          </div>}

      </>
    );
  }
}
