import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/YearBuilt';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class LeadPaint extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.YearBuiltFields
    };

    if (InputValues.BYL_315_Active === undefined || InputValues.BYL_315_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='Lead Paint (PRE 1978)' />
        <div className='form_inputs'>
          <div className='t_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'BYL_140_n' })}</div>
        </div>

        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYL_925_Lead_Based_Paint' })}
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYL_925_Lead_Based_Paint_Explanation' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYL_673_Lead_Based_Paint' })}
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYL_673_Lead_Based_Paint_Explanation' })}
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYL_673_Lead_Based_Paint_Reports' })}
        </div>
      </>
    );
  }
}
