import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class Utilities extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Utilities' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_285_Water_Source' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_285_008_t_Text_Explanation' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_185_Surcharges_Penalities' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_185_t_Text_Explanation' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_665_Water_Dispose' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_665_t_Text_Explanation' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_213_Is_Gas_Suppiled' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_656_How_Gas_Supplied' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_820_Is_Gas_Shut_Off_Installed' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_820_n' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_670_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFU_670_t_Text_Explanation' })}
        </div>
      </>
    );
  }
}
