import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class PropertyMaintenance extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Property Maintenance' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFM_031_Ongoing_Maintenance_Services' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFM_031_Other_Services_Explanation' })}
        </div>
      </>
    );
  }
}
