import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class Pool extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Pool' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFP_536_Pool_Features' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFP_977_Pool_Heat_Type' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFP_977_Spa_Heat_Type' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFP_230_Safety_Features' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFP_817_Has_Pool_Spa_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFP_817_Pool_Spa_Defects_Explanation' })}
        </div>
      </>
    );
  }
}
