import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as FieldDef from '../../../shared/FieldDefinition/Listing';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import ClosingCostsCalc from '../../../shared/ClosingCostsCalc';
import Slider from 'rc-slider';
import '../../../css/rc-slider.css';
import numeral from 'numeral';
import { ContactUsForm } from '../../ContactUs/ContactUsForm';
import uuidv4 from 'uuid/v4';

export default class ListingPrice extends React.Component {
  constructor () {
    super();
    this.state = {
      listPrice: undefined,
      listPriceRange: undefined,
      propertyState: undefined,
      propertyCity: undefined
    };
  }

  componentDidUpdate (prevProps) {
    if (prevProps.Property !== this.props.Property) {
      const { Property, InputValues } = this.props;
      var listPriceRange;
      var listPrice = this.state.listPrice;

      if (Property !== undefined && Property[Consts.DATA_SECTION_PROPERTY_LIST_PRICE_RANGE] !== undefined) {
        listPriceRange = Property[Consts.DATA_SECTION_PROPERTY_LIST_PRICE_RANGE];
        const minRounded = parseInt(Math.ceil(listPriceRange.Min / 1000)) * 1000;
        const maxRounded = parseInt(Math.floor(listPriceRange.Max / 1000)) * 1000;
        const defaultValue = parseInt(Math.floor((listPriceRange.Min + listPriceRange.Max) / 2000)) * 1000;
        listPriceRange = { ...listPriceRange, MinRounded: minRounded, MaxRounded: maxRounded, DefaultValue: defaultValue };
      }

      var propertyState, propertyCounty, propertyCity;
      if (Property !== undefined && Property[Consts.DATA_SECTION_PROPERTY_DATA] !== undefined) {
        const propertyData = Property[Consts.DATA_SECTION_PROPERTY_DATA];
        propertyState = propertyData.State;
        propertyCounty = propertyData.County;
        propertyCity = propertyData.City;
      }

      if (InputValues.Listing_LMF_432_List_Price !== undefined) {
        listPrice = InputValues.Listing_LMF_432_List_Price;
        if (listPriceRange !== undefined && listPrice < listPriceRange.MinRounded) {
          listPrice = undefined;
          this.props.UpdateInputItemValueHandler('Listing_LMF_432_List_Price', undefined);
        } else if (listPriceRange !== undefined && listPrice > listPriceRange.MaxRounded) {
          listPrice = undefined;
          this.props.UpdateInputItemValueHandler('Listing_LMF_432_List_Price', undefined);
        }
      } else {
        listPrice = listPriceRange !== undefined ? listPriceRange.DefaultValue : undefined;
        this.props.UpdateInputItemValueHandler('Listing_LMF_432_List_Price', listPrice);
      }
      this.setState({ listPriceRange, listPrice, propertyState, propertyCounty, propertyCity });
    }
  }

  render () {
    const {
      CurrentTab,
      ValidationResults
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.ListingFields
    };

    if (CurrentTab !== Consts.ListingPrice) {
      return null;
    }

    const listPrice = this.state.listPrice;
    const listPriceRange = this.state.listPriceRange;

    var listPriceError;
    if (ValidationResults !== undefined) {
      listPriceError = ValidationResults.find(item => item.FieldName === 'Listing_LMF_432_List_Price');
    }

    var Errors = [];
    try {
      const closingCosts = ClosingCostsCalc(
        this.state.listPrice,
        this.props.InputValues.Listing_LMF_088_Total_Loans,
        this.state.propertyCity);

      var ListFee = '-';
      var IncentiveCredit = '-';
      // var BuyerAgentFee = '-';

      // var CityTransferTaxRate = '-';
      var CityTransferTax = '-';
      // var CountyTransferTaxRate = '-';
      var CountyTransferTax = '-';

      var EscrowFee = '-';
      var TitleFee = '-';

      var NaturalHazardReport = '-';
      var RegulationCompliance = '-';

      var HomeWarranty = '-';

      var SurpassNet = '-';
      // var SurpassSave = '-';
      var AdvantageNet = '-';
      // var AdvantageSave = '-';

      if (closingCosts !== undefined) {
        ListFee = closingCosts.BrokerFees.ListFee;
        IncentiveCredit = closingCosts.BrokerFees.IncentiveCredit;
        // BuyerAgentFee = closingCosts.BrokerFees.BuyerAgentFee;

        // CityTransferTaxRate = closingCosts.Taxes.CityTransferTaxRate;
        CityTransferTax = closingCosts.Taxes.CityTransferTax;
        // CountyTransferTaxRate = closingCosts.Taxes.CountyTransferTaxRate;
        CountyTransferTax = closingCosts.Taxes.CountyTransferTax;

        EscrowFee = closingCosts.ClosingCosts.EscrowFee;
        TitleFee = closingCosts.ClosingCosts.TitleFee;

        NaturalHazardReport = closingCosts.RegulatoryRequirements.NaturalHazardReport;
        RegulationCompliance = closingCosts.RegulatoryRequirements.RegulationCompliance;

        HomeWarranty = closingCosts.Other.HomeWarranty;

        SurpassNet = closingCosts.SurpassNet;
        // SurpassSave = closingCosts.SurpassSave;
        AdvantageNet = closingCosts.AdvantageNet;
        // AdvantageSave = closingCosts.AdvantageSave;

        Errors = closingCosts.Errors;
      }
    } catch (error) {
      Error.push(error);
    }

    return (
      <>

        <div id='step3' className='main_form_info steppers'>
          <div className='title2'><div className='number_title'>03</div> Listing</div>
          <div className='b_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Listing_LMF_811_Label' })}</div>
          <div className='pro_tab'>
            <div className='tab1'>Listing Details</div>
            <div className='tab_divider desktop' />
            <div className='tab2 desktop'>We use human intelligence and machine learning to determine value.</div>
          </div>
          <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}><span style={{ color: '#111' }}>Note:</span> We use human intelligence and machine learning to determine value.</div>
          <div className='form_inputs'>
            <div className='t_texts'>Choose Your List Price</div>
            {
              listPriceError !== undefined &&
                <>
                  <div style={{ color: 'red' }}>
                    {listPriceError.ValidationError}
                  </div>
                </>
            }
            <div className='container list_price_container'>
              {
                listPriceRange !== undefined &&
                  <>
                    <Slider
                      disabled={this.props.ReadOnly}
                      step={1000}
                      min={listPriceRange.MinRounded}
                      max={listPriceRange.MaxRounded}
                      defaultValue={listPriceRange.DefaultValue}
                      value={listPrice}
                      onChange={(value) => { this.setState({ listPrice: value }); }}
                      onAfterChange={(value) => { this.props.UpdateInputItemValueHandler('Listing_LMF_432_List_Price', value); }}
                    />
                    <div className='list_price_slider_tooltips'>
                      <div className='list_price_slider_tooltips_item'>{listPriceRange.MinFormatted}</div>
                      <div className='list_price_slider_tooltips_item list_price_selected_price'>{numeral(listPrice).format('$0,0')}</div>
                      <div className='list_price_slider_tooltips_item'>{listPriceRange.MaxFormatted}</div>
                    </div>
                  </>
              }

            </div>

            {
              this.props.ReadOnly !== true &&
                <div className='t_texts'>If you want to change the range of your sale price, please click <ContactUsForm handleSubmit={this.props.SubmitContactUsHandler} requestType='List price change request'><span className='blue_text'>here</span></ContactUsForm> and send us a request.</div>
            }
          </div>

          <div className='pro_tab alternatess'>
            <div className='tab1'>Loan Payoff</div>
          </div>

          <div className='form_inputs'>
            <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Listing_LMF_088_Total_Loans' })}</div>
            <div className='t_texts'>{InputHelper.GetFieldTitle({ ...FieldProps, FieldName: 'Listing_LMF_088_Label' })}</div>
          </div>

          <div className='pro_tab alternatess'>
            <div className='tab1'>Closing Costs</div><div className='tab_divider desktop' /><div className='tab2 desktop'>Estimated Closing Costs.</div>
          </div>
          <div className='form_inputs greyer mobile' style={{ marginLeft: '0', marginBottom: '20px' }}><span style={{ color: '#111' }}>Note:</span> Estimated Closing Costs.</div>

          {
            Errors.length > 0 &&
              <div style={{ color: 'red', fontSize: '1.2rem', paddingBottom: '25px' }}>
                <div>Error in calculation of closing costs. please contact advisor.</div>
                <ul>
                  {Errors.map(error => {
                    return (<li key={uuidv4()}>{error}</li>);
                  })}
                </ul>
              </div>
          }
          <div className='closing_costs_container'>
            <div className='closing_costs_left_panel'>
              <div className='form_title2 closing_costs_title2'>Your Listing Price:</div>
              <div className='closing_costs_row'>
                <div className='closing_costs_title'>Nile List Fee: </div>
                <div className='closing_costs_item'>{numeral(ListFee).format('$0,0')}</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>Buyer Incentive Credit: </div>
                <div className='closing_costs_item'>{numeral(IncentiveCredit).format('$0,0')}</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>Total Closing Costs: </div>
                <div className='closing_costs_item'>TBD</div>
              </div>

              <div className='form_title2 closing_costs_title2'>Your Net Proceeds:</div>
              <div className='closing_costs_row'>
                <div className='closing_costs_item'>{numeral(SurpassNet).format('$0,0')} (Nile Preferred)</div>
              </div>
              <div className='closing_costs_row'>
                <div className='closing_costs_item'>{numeral(AdvantageNet).format('$0,0')} (Nile Collbaoration)</div>
              </div>
            </div>
            <div className='closing_costs_right_panel'>
              <div className='form_title2 closing_costs_title2'>Closing Cost Breakdown:</div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>Seller Escrow Fees:</div>
                <div className='closing_costs_item'>{numeral(EscrowFee).format('$0,0')}</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>Seller Title Policy:</div>
                <div className='closing_costs_item'>{numeral(TitleFee).format('$0,0')}</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>County Transfer Tax:</div>
                <div className='closing_costs_item'>{numeral(CountyTransferTax).format('$0,0')}</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>City Transfer Tax:</div>
                <div className='closing_costs_item'>{numeral(CityTransferTax).format('$0,0')}</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>HOA Transfer Fee:</div>
                <div className='closing_costs_item'>TBD</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>HOA Documents (per HOA):</div>
                <div className='closing_costs_item'>TBD</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>1 Year Home Warranty:</div>
                <div className='closing_costs_item'>{numeral(HomeWarranty).format('$0,0')}</div>
              </div>

              <div className='form_title2 closing_costs_title2'>Reports: </div>
              <div className='closing_costs_row'>
                <div className='closing_costs_title'>Natural Hazard Disclosure:</div>
                <div className='closing_costs_item'>{numeral(NaturalHazardReport).format('$0,0')}</div>
              </div>

              <div className='closing_costs_row'>
                <div className='closing_costs_title'>Government Safety and Compliance:</div>
                <div className='closing_costs_item'>{numeral(RegulationCompliance).format('$0,0')}</div>
              </div>
            </div>
          </div>

          <FooterMiddleStep
            HideSaveButton={this.props.ReadOnly}
            SaveHandler={async () => { await this.props.SaveHandler(DataSections); }}
            PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.ListingVesting); }}
            SaveNextHandler={async () => {
              if (this.props.ReadOnly === true) {
                this.props.GoToStepHandler(Consts.ListingVerification);
              } else {
                this.props.SaveNextHandler(Consts.ListingVerification, DataSections);
              }
            }}
          />
        </div>
      </>
    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_PROPERTY_LMF
];
