import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class GeneralStructureFeatures extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='General Structure Features' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_062_Foundation_Type' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_062_Has_Slab_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_062_370_Slab_Defects_Explanation' })}

          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_062_Has_Foundation_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_062_475_Foundation_Defects_Explanation' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_454_Structure_Features' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_454_Structure_Features_Other_Explanation' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_070_Roof_Type' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_070_Roof_Type_Explanation' })}
        </div>

        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_461_Roof_Age' })}
        </div>

        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_1272_Has_Roof_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_1272_Has_Roof_Defects_Explanation' })}
        </div>

        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_605_Has_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_6237_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFG_6237_Defects_Explanation' })}
        </div>
        <div />
      </>);
  }
}
