import React from 'react';
import FooterSaveSubmit from './FooterSaveSubmit';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingVerificationActions extends React.Component {
  render () {
    if (this.props.NeedVerification !== true) {
      return null;
    }

    if (this.props.VestingSuspended) {
      return null;
    }

    if (this.props.InputValues.Vesting_Property_Data_Correct !== Consts.OPTION_YES_VALUE) {
      return null;
    }

    const { InputValues } = this.props;

    return (
      <>
        {
          InputValuesHelper.GetInputValue(InputValues, 'Vesting_Property_Data_Correct') === Consts.OPTION_YES_VALUE &&
          this.props.VestingSuspended !== true &&
          (InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_LLC') !== Consts.OPTION_YES_VALUE &&
          InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_Corporation') !== Consts.OPTION_YES_VALUE &&
          InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_Trustee') !== Consts.OPTION_YES_VALUE) &&
            <FooterSaveSubmit
              SaveHandler={async () => {
                this.props.SaveHandler([Consts.DATA_SECTION_PROPERTY_VESTING]);
              }}
              PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.ListingWelcome); }}
              SubmitHandler={async () => {
                const result = await this.props.SaveHandler([Consts.DATA_SECTION_PROPERTY_VESTING]);
                if (result === true) {
                  this.props.UpdatePropertyStatusHandler('VestingSuspended');
                }
              }}
            />
        }
      </>
    );
  }
}
