import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class FinalQuestions extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Final Questions' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFF_493_Other_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFF_493_Other_Defects_Explanation' })}
        </div>
      </>
    );
  }
}
