import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class HeatingCooling extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Heating & Cooling' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFH_079_Heating_Method' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFH_805_Cooling_Method' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFH_977_Has_Fireplace' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFH_977_Fireplace_Number' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFH_977_Fireplace_Items' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFH_218_Has_Heating_Cooling_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFH_218_Defects_Text_Explanation' })}
        </div>
      </>
    );
  }
}
