import React from 'react';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';
import * as InputHelper from '../../Shared/InputHelper';
import * as FieldDef from '../../../shared/FieldDefinition/YearBuilt';

export default class PolyPipes extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    if (InputValues.BYP_502_Active === undefined || InputValues.BYP_502_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }
    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.YearBuiltFields
    };

    return (
      <>
        <FormSectionTitle Title='PolyPipes (PRE 1995)' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYP_983_PolyPipes' })}
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYP_983_PolyPipes_Explanation' })}
        </div>
      </>
    );
  }
}
