
export default function CalcClosingCosts (listPrice, totalLoanInput, propertyCity) {
  if (listPrice === undefined || totalLoanInput === undefined || propertyCity === undefined) {
    return undefined;
  }

  var errors = [];

  const totalLoan = parseInt(totalLoanInput);

  var listFee = 0;
  if (listPrice < 1000000) {
    listFee = 10000;
  } else if (listPrice < 2000000) {
    listFee = 15000;
  } else {
    listFee = 20000;
  }

  const incentiveCredit = 5000;
  const buyerAgentFeeRate = 0.025;
  const escrowFee = 1000;
  const naturalHazardReport = 125;
  const regulationCompliance = 175;
  const surpassSavePer = 0.06;
  const traditionalListingAgentFeePer = 0.03;
  const homeWarranty = 550;
  const countyTransferTaxRate = 0.0011;

  const buyerAgentFee = listPrice * buyerAgentFeeRate;
  const countyTransferTax = Math.round(listPrice * countyTransferTaxRate);

  var cityTransferTaxRate;
  var cityTransferTax;
  const transferTaxItem = TransferTax.find(item => item.City.toUpperCase() === propertyCity.toUpperCase());
  if (transferTaxItem !== undefined) {
    cityTransferTaxRate = transferTaxItem.CityTransferTaxRate;
    // countyTransferTaxRate = transferTaxItem.CountyTransferTaxRate;
    cityTransferTax = Math.round(listPrice * cityTransferTaxRate);
    // countyTransferTax = Math.round(listPrice * countyTransferTaxRate);
  } else {
    errors.push(`Missing transfer tax setting for city ${propertyCity}`);
  }

  var titleFee;
  const titleFeeItem = TitleFees.find(item => item.ListPrice >= listPrice);
  if (titleFeeItem !== undefined) {
    titleFee = titleFeeItem.TitleFee;
  } else {
    errors.push(`Missing title fee setting for ${propertyCity}`);
  }

  const surpassNet = listPrice - (totalLoan + listFee + incentiveCredit + countyTransferTax + cityTransferTax + escrowFee + titleFee + naturalHazardReport + regulationCompliance + homeWarranty);
  const surpassSave = (listPrice * surpassSavePer) - listFee; // where 0.055 from, what is 15000,is it list fee?
  const advantageNet = listPrice - (totalLoan + listFee + buyerAgentFee + countyTransferTax + cityTransferTax + escrowFee + titleFee + naturalHazardReport + regulationCompliance + homeWarranty);
  const advantageSave = (listPrice * traditionalListingAgentFeePer) - listFee; // what is 15000,is it list fee?

  const result =
      {
        BrokerFees: {
          ListFee: listFee,
          IncentiveCredit: incentiveCredit,
          BuyerAgentFee: buyerAgentFee
        },
        Taxes: {
          CityTransferTaxRate: cityTransferTaxRate,
          CityTransferTax: cityTransferTax,
          CountyTransferTaxRate: countyTransferTaxRate,
          CountyTransferTax: countyTransferTax
        },
        ClosingCosts: {
          EscrowFee: escrowFee,
          TitleFee: titleFee
        },
        RegulatoryRequirements: {
          NaturalHazardReport: naturalHazardReport,
          RegulationCompliance: regulationCompliance
        },
        Other: {
          HomeWarranty: homeWarranty
        },
        SurpassNet: surpassNet,
        SurpassSave: surpassSave,
        AdvantageNet: advantageNet,
        AdvantageSave: advantageSave,
        Errors: errors
      };

  return result;
}

