import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class RegulatedBuildingAndSafety extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='REGULATED BUILDING AND SAFETY FEATURES' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFR_043_Consent_Title' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFR_Installed_Items' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFR_043_Consent_Note' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFR_Consent' })}
        </div>
      </>
    );
  }
}
