import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class KitchenFeatures extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Kitchen Features' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFK_x01_Kitchen_Features' })}
        </div>
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFK_460_Has_Defects' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFK_460_Defects_Explanation' })}
        </div>
      </>
    );
  }
}
