import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/YearBuilt';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class Asbestos extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.YearBuiltFields
    };

    if (InputValues.BYA_581_Active === undefined || InputValues.BYA_581_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='Asbestos (PRE 1978)' />

        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYA_239_Asbestos' })}
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYA_239_Asbestos_Explanation' })}
        </div>
      </>
    );
  }
}
