import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class LeasedLienedItems extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Leased or Liened Items' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFD_998_Has_Leased_Liened_Items' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFD_762_Leased_Liened_Items' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFD_762_Other_Items_Explanation' })}
        </div>
      </>
    );
  }
}
