import React from 'react';

export const GetValidationResult = (validationResults, fieldName) => {
  if (validationResults !== undefined) {
    const result = validationResults.find(item => item.FieldName === fieldName);
    if (result !== undefined) {
      return (
        <>
          <span className='validation-failed' />
          <span className='__validation_error_text __validation_error'>
            {result.ValidationError}
          </span>
        </>
      );
    }
  }
};

export const GetClassName = (validationResults, fieldName, required) => {
  var className = 'form2';
  if (required) {
    className += ' required';
  }

  if (validationResults !== undefined) {
    const result = validationResults.find(item => item.FieldName === fieldName);
    if (result !== undefined) {
      className += ' validation-failed';
    }
  }
  return className;
};
