import React from 'react';
import FooterMiddleStep from './FooterMiddleStep';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';

export default class VestingSellerProfileActions extends React.Component {
  render () {
    const {
      InputValues,
      NeedCompleteBy,
      NeedVerification
    } = this.props;

    if (InputValuesHelper.GetInputValue(InputValues, 'Vesting_Property_Data_Correct') === Consts.OPTION_NO_VALUE) {
      return null;
    }

    if (NeedCompleteBy === true && (InputValues.Vesting_Form_Completed_By === undefined || InputValues.Vesting_Form_Completed_By === '_not_listed_')) {
      return null;
    }

    const verifiedLLC = InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_LLC');
    const verifiedCorporation = InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_Corporation');
    const verifiedTrustee = InputValuesHelper.GetInputValue(InputValues, 'Vesting_Verified_Trustee');

    if (NeedVerification === true &&
      (verifiedLLC === undefined || verifiedLLC === Consts.OPTION_NO_VALUE) &&
      (verifiedCorporation === undefined || verifiedCorporation === Consts.OPTION_NO_VALUE) &&
      (verifiedTrustee === undefined || verifiedTrustee === Consts.OPTION_NO_VALUE)) {
      return null;
    }

    return (
      <>
        <FooterMiddleStep
          HideSaveButton={this.props.ReadOnly}
          SaveHandler={async () => {
            await this.props.SaveHandler(DataSections);
          }}
          PreviousStepHandler={() => { this.props.GoToStepHandler(Consts.ListingWelcome); }}
          SaveNextHandler={async () => {
            if (this.props.ReadOnly === true) {
              this.props.GoToStepHandler(Consts.ListingPrice);
            } else {
              this.props.SaveNextHandler(Consts.ListingPrice, DataSections);
            }
          }}
        />
      </>
    );
  }
}

const DataSections = [
  Consts.DATA_SECTION_PROPERTY_VESTING,
  Consts.DATA_SECTION_PROPERTY_LMH
];
