import React from 'react';
import * as Consts from '../../../shared/Consts';

export default class Navigator extends React.Component {
  render () {
    const stepNavClasses = 'step_btn animated_fastest';
    const stepNavActiveClasses = 'step_btn animated_fastest step_active';
    const {
      CurrentTab
      // onStepNavClick
    } = this.props;

    return (
      <>
        <div className='form_title'>Welcome to NILE</div>
        <div className='stickit'>
          <div className='steps'>
            <div id={Consts.ListingWelcome} className={CurrentTab === Consts.ListingWelcome ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />1
              <div className='down_border' />
            </div>
            <div id={Consts.ListingVesting} className={CurrentTab === Consts.ListingVesting ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />2
              <div className='down_border' />
            </div>
            <div id={Consts.ListingPrice} className={(CurrentTab === Consts.ListingPrice) ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />3
              <div className='down_border' />
            </div>
            <div id={Consts.ListingVerification} className={(CurrentTab === Consts.ListingVerification || CurrentTab === Consts.ListingSubmission) ? stepNavActiveClasses : stepNavClasses}>
              <div className='up_border' />4
              <div className='down_border' />
            </div>
          </div>
        </div>
      </>
    );
  }
}
