import React from 'react';
import Welcome from '../../components/SellerProperty/Listing/Welcome';
import Vesting from '../../components/SellerProperty/Listing/Vesting';
import ListingPrice from '../../components/SellerProperty/Listing/ListingPrice';
import ListingVerification from '../../components/SellerProperty/Listing/ListingVerification';
import ListingSubmission from '../../components/SellerProperty/Listing/ListingSubmission';
import Navigator from '../../components/SellerProperty/Listing/Navigator';
import axios from 'axios';
import * as Consts from '../../shared/Consts';
import * as Validator from '../../shared/Validator';
import * as FieldDef from '../../shared/FieldDefinition/Listing';
import * as PostDataGenerator from '../../shared/PostDataGenerator';
import * as ResponseDataConvert from '../../shared/ResponseDataConvert';

import './Listing.css';

export default class Listing extends React.Component {
  constructor (props) {
    super();
    this.SaveHandler = this.SaveHandler.bind(this);
    this.SaveNextHandler = this.SaveNextHandler.bind(this);
    this.handleStepNavClick = this.handleStepNavClick.bind(this);
    this.ValueChangeHandler = this.ValueChangeHandler.bind(this);
    this.handleGoToStep = this.handleGoToStep.bind(this);
    this.UpdateInputItemValue = this.UpdateInputItemValue.bind(this);
    this.SubmitContactUs = this.SubmitContactUs.bind(this);

    var startStep = Consts.ListingWelcome;

    this.state = {
      PropertyId: props.PropertyId,
      Property: undefined,
      SelectedTab: startStep,
      InputValues: {},
      InputValuesChanged: {},
      ValidationResults: undefined
    };
  }

  async componentDidMount () {
    await this.LoadData();
  }

  async LoadData () {
    try {
      this.props.StartLoading();
      this.setState({ InputValuesChanged: {} });
      const requestConfig = await this.props.GetRequestConfig();

      if (this.state.PropertyId !== undefined) {
        const includes = [
          Consts.DATA_SECTION_PROPERTY_DATA,
          Consts.DATA_SECTION_PROPERTY_VESTING,
          Consts.DATA_SECTION_PROPERTY_LIST_PRICE_RANGE,
          Consts.DATA_SECTION_PROPERTY_LMH,
          Consts.DATA_SECTION_PROPERTY_LMA,
          Consts.DATA_SECTION_PROPERTY_LMF
        ];

        const url = `${this.props.DataSectionAPIUrl}/?include=${includes.join(',')}`;
        const response = await axios.get(url, requestConfig);
        const property = response.data.Property;

        let inputValues = this.state.InputValues;
        if (inputValues === undefined) {
          inputValues = {};
        }

        var fieldDefs = FieldDef.ListingFields;
        includes.forEach(dataSection => {
          const result = this.ConvertData(dataSection, fieldDefs, response.data);
          inputValues = { ...inputValues, ...result };
        });

        [1, 2, 3, 4].forEach(index => {
          var fieldName = `Listing_LMH_Seller_${index + 1}_active`;
          inputValues[fieldName] = Consts.OPTION_NO_VALUE;
        });
        if (property[Consts.DATA_SECTION_PROPERTY_DATA] !== undefined &&
          property[Consts.DATA_SECTION_PROPERTY_DATA].OwnerNames !== undefined) {
          const ownerNames = property[Consts.DATA_SECTION_PROPERTY_DATA].OwnerNames.split('/');
          ownerNames.forEach((_ownerName, index) => {
            if (index < 4) {
              var fieldName = `Listing_LMH_Seller_${index + 1}_active`;
              inputValues[fieldName] = Consts.OPTION_YES_VALUE;
            }
          });
        }
        this.setState({ InputValues: inputValues, Property: property });
      } else {
        this.setState({ InputValues: undefined, Property: undefined });
      }
    } catch (error) {
      console.log(error);
      this.props.NotifyError('Load listing info', 'Please refresh page and try again');
    } finally {
      this.props.EndLoading();
    }
  }

  async SaveNextHandler (nextStep, dataSections) {
    const result = await this.SaveData(false, dataSections);
    if (result) {
      this.handleGoToStep(nextStep);
    }
  }

  async SaveHandler (dataSections) {
    const result = await this.SaveData(true, dataSections);
    return result;
  }

  async SaveData (displaySucceededMessage, dataSections) {
    if (dataSections === undefined || dataSections.length === 0) {
      if (displaySucceededMessage) {
        this.props.NotifySuccess('Save listing info', 'Succeeded');
      }
      return true;
    }

    const validationResults = this.ValidateInputValue(dataSections, this.state.InputValues);
    this.setState({ ValidationResults: validationResults });
    if (validationResults !== undefined && validationResults.length > 0) {
      return false;
    }

    this.props.StartLoading();
    const selectedTab = this.state.SelectedTab;

    try {
      await this.SaveStepData(selectedTab, dataSections);
      this.UpdateInputValuesChanged(selectedTab, false);

      if (displaySucceededMessage === true) {
        this.props.NotifySuccess('Save listing info', 'Succeeded');
      }
      return true;
    } catch (Error) {
      console.log(Error);
      this.props.NotifyError('Save listing info', 'Please refresh page and try again');
      return false;
    } finally {
      this.props.EndLoading();
    }
  }

  async SaveStepData (selectedTab, dataSections) {
    // skip if data not changed
    if (this.state.InputValuesChanged[selectedTab] === undefined || this.state.InputValuesChanged[selectedTab] === false) {
      return;
    }

    // save data
    const fieldDefs = FieldDef.ListingFields;
    const postData = {};
    dataSections.forEach(section => {
      postData[section] = PostDataGenerator.GetPostData(this.state.InputValues, fieldDefs, section);
    });

    const body = {
      Data: postData
    };
    const requestConfig = await this.props.GetRequestConfig();
    await axios.post(this.props.DataSectionAPIUrl, body, requestConfig);
  }

  ConvertData (dataSection, fieldDefs, responseData) {
    const sectionData = responseData.Property[dataSection];
    const sectionDataResult = ResponseDataConvert.Convert(sectionData, fieldDefs, dataSection);
    return sectionDataResult;
  }

  async SubmitContactUs (message, requestType) {
    var result;
    try {
      await this.SubmitRequest(message, requestType);
      this.props.NotifySuccess('Contact US', 'You request has been submitted.');
      result = true;
    } catch (error) {
      result = false;
      console.log(error);
      var errorMessage = 'Please refresh page and try again';
      if (error.response.status === 404) {
        if (error.response.data !== undefined && error.response.data.Message !== undefined) {
          errorMessage = error.response.data.Message;
        }
      }
      this.props.NotifyError('Failed to submit your request', errorMessage);
    }
    return result;
  }

  async SubmitRequest (message, requestType) {
    const body = {
      Message: `${requestType !== undefined ? requestType + '\n\n' : ''}${message}`
    };
    const requestConfig = await this.props.GetRequestConfig();
    await axios.post(`${Consts.API_SELLER_PROPERTIES}/${this.state.PropertyId}/contactus`, body, requestConfig);
  }

  async ValueChangeHandler (event, fieldName, fieldType, value) {
    const inputValues = this.state.InputValues;

    var targetValue;
    if (fieldName !== undefined && fieldType !== undefined) {
      targetValue = value;
      inputValues[fieldName] = value;
    } else {
      if (event.target.type === 'checkbox') {
        targetValue = event.target.checked;
        inputValues[event.target.name] = targetValue;
      } else {
        targetValue = event.target.value;
        inputValues[event.target.name] = targetValue;
      }
    }

    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  UpdateInputItemValue (fieldName, value) {
    const inputValues = this.state.InputValues;
    inputValues[fieldName] = value;
    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  UpdateInputValuesChanged (step, value) {
    const inputValuesChanged = this.state.InputValuesChanged;
    inputValuesChanged[step] = value;
    this.setState({ InputValuesChanged: inputValuesChanged });
  }

  handleStepNavClick (event, dataSections) {
    if (this.state.SelectedTab === Consts.ListingVesting || this.state.SelectedTab === Consts.ListingPrice) {
      const validationResults = this.ValidateInputValue(dataSections, this.state.InputValues);
      this.setState({ ValidationResults: validationResults });
      if (validationResults === undefined || validationResults.length === 0) {
        this.setState({ SelectedTab: event.target.id });
      }
    } else {
      this.setState({ SelectedTab: event.target.id });
    }
  }

  ValidateInputValue (dataSections, inputValues) {
    const fieldDefs = FieldDef.ListingFields;
    var validateFields = [];
    if (dataSections !== undefined) {
      dataSections.concat(Consts.DATA_SECTION_PROPERTY_DATA).forEach(dataSection => {
        const dataFields = fieldDefs.filter(field => field.DataSection === dataSection);
        dataFields.forEach(dataField => {
          validateFields.push(dataField);
        });
      });
    }

    return Validator.Validate(inputValues, validateFields);
  }

  handleGoToStep (step) {
    this.setState({ SelectedTab: step });
    window.scrollTo(0, 0);
  }

  render () {
    var readOnly;
    var vestingSuspended = false;
    if (this.state.Property !== undefined) {
      readOnly = (this.state.Property.VestingSuspended === true || this.state.Property.ListingSubmitted === true);
      vestingSuspended = (this.state.Property.VestingSuspended === true);
    }

    const updatePropertyStatusFunc = async (name) => {
      if (await this.props.UpdatePropertyStatusHandler(name) === true) {
        this.LoadData();
      }
    };

    const stepProps = {
      CurrentTab: this.state.SelectedTab,
      SaveHandler: this.SaveHandler,
      SaveNextHandler: this.SaveNextHandler,
      ValueChangeHandler: this.ValueChangeHandler,
      GoToStepHandler: this.handleGoToStep,
      UpdateInputItemValueHandler: this.UpdateInputItemValue,
      SubmitContactUsHandler: this.SubmitContactUs,
      UpdatePropertyStatusHandler: updatePropertyStatusFunc,
      ValidationResults: this.state.ValidationResults,
      InputValues: this.state.InputValues,
      Property: this.state.Property,
      NotifyError: this.props.NotifyError,
      NotifySuccess: this.props.NotifySuccess,
      NotifyWarning: this.props.NotifyWarning,
      ReadOnly: readOnly,
      VestingSuspended: vestingSuspended
    };

    return (
      <>
        <Navigator
          CurrentTab={this.state.SelectedTab}
          SaveNextHandler={this.SaveNextHandler}
          onStepNavClick={this.handleStepNavClick}
        />
        <Welcome {...stepProps} />
        <Vesting {...stepProps} />
        <ListingPrice {...stepProps} />
        <ListingVerification {...stepProps} />
        <ListingSubmission {...stepProps} />
      </>
    );
  }
}
