import * as Consts from './Consts';

export const Convert = (responseData, fieldDefinitions, dataSection) => {
  const result = {};

  if (responseData !== undefined) {
    const fields = fieldDefinitions.filter(item => item.DataSection === dataSection);
    for (const field of fields) {
      if (field.DataType === Consts.DATA_TYPE_ARRAY) {
        const tableItems = responseData[field.DataField];
        if (Array.isArray(tableItems)) {
          for (const tableItem of tableItems) {
            const rowNumberCol = field.Cols.find(col => col.Type === Consts.FIELD_TYPE_TABLE_ROW_NUMBER);
            const rowNumberField = rowNumberCol === undefined ? 'Unknown' : rowNumberCol.DataField;
            const rowNumber = tableItem[rowNumberField];
            for (const col of field.Cols) {
              if (col.DataField !== rowNumberField) {
                const colFieldName = `${col.FieldName}_${rowNumber}`;
                if (col.DataType === Consts.DATA_TYPE_BOOLEAN && col.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
                  if (tableItem[colFieldName] === true) {
                    result[colFieldName] = Consts.OPTION_YES_VALUE;
                  } else if (tableItem[colFieldName] === false) {
                    result[colFieldName] = Consts.OPTION_NO_VALUE;
                  } else {
                    result[colFieldName] = undefined;
                  }
                } else {
                  result[colFieldName] = tableItem[col.DataField];
                }
              }
            }
          }
        }
      } else {
        if (responseData[field.DataField] !== undefined && responseData[field.DataField] !== null) {
          if (field.DataType === Consts.DATA_TYPE_BOOLEAN && field.Type === Consts.FIELD_TYPE_YES_NO_OPTIONS) {
            if (responseData[field.DataField] === true) {
              result[field.FieldName] = Consts.OPTION_YES_VALUE;
            } else if (responseData[field.DataField] === false) {
              result[field.FieldName] = Consts.OPTION_NO_VALUE;
            } else {
              result[field.FieldName] = undefined;
            }
          } else {
            result[field.FieldName] = responseData[field.DataField];
          }
        } else {
          result[field.FieldName] = undefined;
        }
      }
    }
  }

  return result;
};
