import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/Listing';
import * as InputHelper from '../../Shared/InputHelper';
import * as InputValuesHelper from '../../../shared/InputValuesHelper';
import * as Consts from '../../../shared/Consts';
import uuidv4 from 'uuid/v4';

export default class VestingConfirmPropertyData extends React.Component {
  render () {
    const {
      InputValues,
      PropertyData
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.ListingFields
    };

    var propertyAddress, vestingType, ownerNames;
    if (PropertyData !== undefined) {
      propertyAddress = Consts.ConvertDTPropertyAddress(PropertyData);
      vestingType = PropertyData.VestingOwnershipRight;
      ownerNames = PropertyData.OwnerNames.split('/');
    }

    return (
      <>
        <div className='pro_tab'>
          <div className='tab1'>Property Data</div>
        </div>
        <div className='form_inputs'>
          <div className='property_data_row'>
            <div className='t_texts property_data_title'>Property Address:</div>
            <div className='t_texts property_data_item'>{propertyAddress}</div>
          </div>
          <div className='property_data_row'>
            <div className='t_texts property_data_title'>Vesting Type:</div>
            <div className='t_texts property_data_item'>{vestingType}</div>
          </div>

          {
            ownerNames.map((ownerName, index) => {
              return (
                <div className='property_data_row' key={uuidv4()}>
                  <div className='t_texts property_data_title'>Owner {index + 1}:</div>
                  <div className='t_texts property_data_item'>{ownerName.trim()}</div>
                </div>
              );
            }
            )
          }
        </div>
        <div className='form_inputs'>
          <div className='inputer_form thirds'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Vesting_Property_Data_Correct' })}</div>
          {
            InputValuesHelper.GetInputValue(InputValues, 'Vesting_Property_Data_Correct') === Consts.OPTION_NO_VALUE &&
              <div>
                <div className='inputer_form halfs'>{InputHelper.GenerateInput({ ...FieldProps, FieldName: 'Vesting_Property_Address_Override' })}</div>
              </div>
          }
        </div>

        {
          (InputValues.Vesting_Property_Data_Correct === Consts.OPTION_NO_VALUE && this.props.VestingSuspended === true) &&
            <div className='b_texts' style={{ color: '#000' }}>
              <div>A NILE advisor will be in touch shortly.</div>
            </div>
        }
      </>
    );
  }
}
