import React from 'react';
import * as FieldDef from '../../../shared/FieldDefinition/YearBuilt';
import * as InputHelper from '../../Shared/InputHelper';
import * as Consts from '../../../shared/Consts';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class ImpactGlazing extends React.Component {
  render () {
    const {
      InputValues
    } = this.props;

    const FieldProps = {
      ...this.props,
      FieldDefs: FieldDef.YearBuiltFields
    };

    if (InputValues.BYG_586_Active === undefined || InputValues.BYG_586_Active === Consts.OPTION_NO_VALUE) {
      return null;
    }

    return (
      <>
        <FormSectionTitle Title='Impact Glazing (PRE 1984)' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYG_098_Impact_Glazing' })}
          {InputHelper.GenerateInputerForm({ ...FieldProps, FieldName: 'BYG_098_Impact_Glazing_Explanation' })}
        </div>
      </>
    );
  }
}
