import React from 'react';
import * as InputHelper from '../../Shared/InputHelper';
import FormSectionTitle from '../../Shared/FormSectionTitle';

export default class VehicleParking extends React.Component {
  render () {
    return (
      <>
        <FormSectionTitle Title='Vehicle & Parking' />
        <div className='form_inputs'>
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFV_300_Has_Parking' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFV_056_Spaces' })}
          {InputHelper.GenerateInputerForm({ ...this.props, FieldName: 'SFV_056_Parking_Space_Items' })}
        </div>
      </>
    );
  }
}
