import React from 'react';
import Navigator from '../../components/SellerProperty/SystemsFeatures/Navigator';
import ActionsBar from '../../components/SellerProperty/SystemsFeatures/ActionsBar';
import Utilities from '../../components/SellerProperty/SystemsFeatures/Utilities';
import GeneralStructureFeatures from '../../components/SellerProperty/SystemsFeatures/GeneralStructureFeatures';
import VehicleParking from '../../components/SellerProperty/SystemsFeatures/VehicleParking';
import PlumbingElectrical from '../../components/SellerProperty/SystemsFeatures/PlumbingElectrical';
import HeatingCooling from '../../components/SellerProperty/SystemsFeatures/HeatingCooling';
import KitchenFeatures from '../../components/SellerProperty/SystemsFeatures/KitchenFeatures';
import RegulatedBuildingAndSafety from '../../components/SellerProperty/SystemsFeatures/RegulatedBuildingAndSafety';
import SafetySecurity from '../../components/SellerProperty/SystemsFeatures/SafetySecurity';
import OtherFeatures from '../../components/SellerProperty/SystemsFeatures/OtherFeatures';
import ExteriorGrounds from '../../components/SellerProperty/SystemsFeatures/ExteriorGrounds';
import Pool from '../../components/SellerProperty/SystemsFeatures/Pool';
import LeasedLienedItems from '../../components/SellerProperty/SystemsFeatures/LeasedLienedItems';
import PropertyMaintenance from '../../components/SellerProperty/SystemsFeatures/PropertyMaintenance';
import OperatingCosts from '../../components/SellerProperty/SystemsFeatures/OperatingCosts';
import FinalQuestions from '../../components/SellerProperty/SystemsFeatures/FinalQuestions';

import axios from 'axios';
import * as Consts from '../../shared/Consts';
import * as Validator from '../../shared/Validator';
import * as FieldDef from '../../shared/FieldDefinition/SystemsFeatures';
import * as PostDataGenerator from '../../shared/PostDataGenerator';
import * as ResponseDataConvert from '../../shared/ResponseDataConvert';

export default class SystemsFeaturesDashboard extends React.Component {
  constructor (props) {
    super(props);
    this.ValueChangeHandler = this.ValueChangeHandler.bind(this);
    this.SaveHandler = this.SaveHandler.bind(this);

    if (props.match !== undefined) {
      var startStep = (props.match.params.step === undefined ? Consts.STEP_1 : props.match.params.step);
    }

    this.state = {
      propertyId: props.PropertyId,
      SelectedTab: startStep,
      InputValues: {},
      InputValuesChanged: {},
      ValidationResults: undefined
    };
  }

  async componentDidMount () {
    try {
      this.props.StartLoading();
      this.setState({ InputValuesChanged: {} });
      const requestConfig = await this.props.GetRequestConfig();

      const includes = [
        Consts.DATA_SECTION_PROPERTY_SFU,
        Consts.DATA_SECTION_PROPERTY_SFG,
        Consts.DATA_SECTION_PROPERTY_SFV,
        Consts.DATA_SECTION_PROPERTY_SFL,
        Consts.DATA_SECTION_PROPERTY_SFH,
        Consts.DATA_SECTION_PROPERTY_SFK,
        Consts.DATA_SECTION_PROPERTY_SFR,
        Consts.DATA_SECTION_PROPERTY_SFS,
        Consts.DATA_SECTION_PROPERTY_SFO,
        Consts.DATA_SECTION_PROPERTY_SFX,
        Consts.DATA_SECTION_PROPERTY_SFP,
        Consts.DATA_SECTION_PROPERTY_SFD,
        Consts.DATA_SECTION_PROPERTY_SFM,
        Consts.DATA_SECTION_PROPERTY_SFC,
        Consts.DATA_SECTION_PROPERTY_SFF
      ];

      const url = `${this.props.DataSectionAPIUrl}?include=${includes.join(',')}`;
      const response = await axios.get(url, requestConfig);

      let inputValues = this.state.InputValues;
      if (inputValues === undefined) {
        inputValues = {};
      }

      var fieldDefs = FieldDef.SystemsFeaturesFields;
      for (const dataSection of includes) {
        const result = this.ConvertData(dataSection, fieldDefs, response.data);
        inputValues = { ...inputValues, ...result };
      }

      this.setState({ InputValues: inputValues });
    } catch (error) {
      console.log(error);
      this.props.NotifyError('Load systems features', 'Please refresh page and try again');
    } finally {
      this.props.EndLoading();
    }
  }

  ConvertData (dataSection, fieldDefs, responseData) {
    const sectionData = responseData.Property[dataSection];
    const sectionDataResult = ResponseDataConvert.Convert(sectionData, fieldDefs, dataSection);
    return sectionDataResult;
  }

  async SaveHandler (event) {
    await this.SaveData(event, false, true);
  }

  async SaveData () {
    try {
      this.props.StartLoading();

      const sections = [
        Consts.DATA_SECTION_PROPERTY_SFU,
        Consts.DATA_SECTION_PROPERTY_SFG,
        Consts.DATA_SECTION_PROPERTY_SFV,
        Consts.DATA_SECTION_PROPERTY_SFL,
        Consts.DATA_SECTION_PROPERTY_SFH,
        Consts.DATA_SECTION_PROPERTY_SFK,
        Consts.DATA_SECTION_PROPERTY_SFR,
        Consts.DATA_SECTION_PROPERTY_SFS,
        Consts.DATA_SECTION_PROPERTY_SFO,
        Consts.DATA_SECTION_PROPERTY_SFX,
        Consts.DATA_SECTION_PROPERTY_SFP,
        Consts.DATA_SECTION_PROPERTY_SFD,
        Consts.DATA_SECTION_PROPERTY_SFM,
        Consts.DATA_SECTION_PROPERTY_SFC,
        Consts.DATA_SECTION_PROPERTY_SFF
      ];

      var fieldsDefs = [];
      sections.forEach(section => {
        const fields = FieldDef.SystemsFeaturesFields.filter(field => field.DataSection === section);
        fieldsDefs = fieldsDefs.concat(fields);
      });

      const validationResults = Validator.Validate(this.state.InputValues, fieldsDefs);
      this.setState({ ValidationResults: validationResults });

      if (validationResults === undefined || validationResults.length === 0) {
        const postData = {};
        for (const section of sections) {
          postData[section] = PostDataGenerator.GetPostData(this.state.InputValues, fieldsDefs, section);
        }

        const body = {
          Data: postData
        };
        const requestConfig = await this.props.GetRequestConfig();
        const response = await axios.post(this.props.DataSectionAPIUrl, body, requestConfig);

        const putFileErrors = [];
        const files = [];
        for (const section of sections) {
          const fileFields = fieldsDefs.filter(item => item.DataSection === section).filter(item => item.Type === Consts.FIELD_TYPE_MULTIPLE_FILES);
          if (fileFields !== undefined) {
            for (const fileField of fileFields) {
              if (postData[section] !== undefined && postData[section][fileField.DataField] !== undefined) {
                for (const file of postData[section][fileField.DataField]) {
                  files.push(file);
                }
              }
            }
          }
        }

        if (files.length > 0) {
          // upload files
          for (let i = 0; i < response.data.urls.length; i++) {
            const urlItem = response.data.urls[i];
            const file = files.find(item => item.id === urlItem.id);
            if (file !== undefined) {
              var options = {
                headers: {
                  'Content-Type': file.type
                }
              };
              try {
                const fileToUpload = file.file;
                await axios.put(urlItem.url, fileToUpload, options);
              } catch (error) {
                putFileErrors.push(file.name);
                this.props.NotifyError('Save systems features', `Failed to upload file ${file.name}`);
              }
            }
          }
        }

        this.props.NotifySuccess('Save systems features', 'Succeeded');
      }
    } catch (error) {
      this.props.NotifyError('Save systems features', 'Please refresh page and try again');
    } finally {
      this.props.EndLoading();
    }
  }

  ValueChangeHandler (event, fieldName, fieldType, value) {
    const inputValues = this.state.InputValues;

    if (fieldName !== undefined && fieldType !== undefined) {
      inputValues[fieldName] = value;
    } else {
      if (event.target.type === 'text') {
        inputValues[event.target.name] = event.target.value;
      } else if (event.target.type === 'checkbox') {
        if (event.target.dataset === undefined) {
          inputValues[event.target.name] = event.target.checked;
        } else if (event.target.dataset.type === 'multiple-choises') {
          const label = event.target.dataset.label;
          const value = event.target.dataset.value;

          var currentSelectedItems = inputValues[event.target.name];
          if (currentSelectedItems === undefined) {
            currentSelectedItems = [];
          } else {
            currentSelectedItems = currentSelectedItems.filter(item => item.value !== value);
          }
          if (event.target.checked) {
            currentSelectedItems.push({ label, value });
          }

          inputValues[event.target.name] = currentSelectedItems;
        } else {
          inputValues[event.target.name] = event.target.checked;
        }
      } else if (event.target.type === 'date') {
        inputValues[event.target.name] = event.target.value;
      } else {
        inputValues[event.target.name] = event.target.value;
      }
    }
    this.setState({ InputValues: inputValues });
    this.UpdateInputValuesChanged(this.state.SelectedTab, true);
  }

  UpdateInputValuesChanged (step, value) {
    const inputValuesChanged = this.state.InputValuesChanged;
    inputValuesChanged[step] = value;
    this.setState({ InputValuesChanged: inputValuesChanged });
  }

  render () {
    const stepProps = {
      SaveHandler: this.SaveHandler,
      // SaveNextHandler: this.SaveNextHandler,
      // PreviousStepHandler: this.PreviousStepHandler,
      ValueChangeHandler: this.ValueChangeHandler,
      ValidationResults: this.state.ValidationResults,
      InputValues: this.state.InputValues,
      FieldDefs: FieldDef.SystemsFeaturesFields
    };

    return (
      <>
        <Navigator />
        <div className='main_form_info steppers'>
          <Utilities {...stepProps} />
          <GeneralStructureFeatures {...stepProps} />
          <VehicleParking {...stepProps} />
          <PlumbingElectrical {...stepProps} />
          <HeatingCooling {...stepProps} />
          <KitchenFeatures {...stepProps} />
          <RegulatedBuildingAndSafety {...stepProps} />
          <SafetySecurity {...stepProps} />
          <OtherFeatures {...stepProps} />
          <ExteriorGrounds {...stepProps} />
          <Pool {...stepProps} />
          <LeasedLienedItems {...stepProps} />
          <PropertyMaintenance {...stepProps} />
          <OperatingCosts {...stepProps} />
          <FinalQuestions {...stepProps} />
        </div>
        <ActionsBar {...stepProps} />
      </>
    );
  }
}
