import React from 'react';

export default class Navigator extends React.Component {
  render () {
    const {
      PropertyData
    } = this.props;

    var yearBuilt = 9999;
    var yearBuiltDisplay = 'UNKNOWN';
    if (PropertyData !== undefined && PropertyData.YearBuilt !== undefined) {
      yearBuilt = parseInt(PropertyData.YearBuilt);
      yearBuiltDisplay = parseInt(PropertyData.YearBuilt);
    }

    return (
      <>
        <div className='step_title step_title_wide'>YEAR BUILT</div>
        {
          PropertyData !== undefined &&
            <>
              <div className='step_texts' style={{ maxWidth: '100%', marginBottom: '40px' }}>
                <div>Property Built Year: {yearBuiltDisplay}</div>
                {
                  (yearBuilt >= 1996 && yearBuilt !== 9999) &&
                    <div style={{ marginTop: '15px' }}>No information required for properties built after 1996.</div>
                }
                {
                  (yearBuilt < 1996 && yearBuilt !== 9999) &&
                    <div style={{ marginTop: '15px' }}>Please fill in the following information.</div>
                }
              </div>
            </>
        }
      </>
    );
  }
}
